<template>
	<view class="modify">
		<image src="http://qp9mr1fhs.hn-bkt.clouddn.com/img/my-bg.jpg" class="bg"></image>
		<view class="information">
			<view class="text">{{company == 0 ? '个人信息编辑':'请完善个人资料'}}</view>
			<view class="content">
				<view class="img">
					<view class="information_text">我的头像</view>
					<uploadimg 
						:width="100"
						:height="100"
						:bgsrc="img"
						@chooseimg="handleChooseImg"
						:model='model'>
					</uploadimg>
					
					<!-- <u-upload
						:width="200"
						:height="200"
						:bgsrc="img"
						@chooseimg="handleChooseImg"
						:auto-upload="false"
						max-count="1"
						:show-progress="false"
						:deletable="false"
						>
					</u-upload> -->
				</view>
				<view class="name" v-if="company != 0">
					<view class="information_text">公司名称</view>
					<text class="information_name">{{company}}</text>
				</view>
				<view class="name">
					<view class="information_text">我的昵称</view>
					<input v-model="name" placeholder="请输入您的昵称" class="information_name" @focus="onHeight" @blur="noInput"  maxlength="8">
				</view>
				<view class="phone" :style="yzm == true ? 'border-bottom: 1rpx solid #f1f1f1;margin-bottom: 0;':''" >
					<view class="information_text">手机号</view>
					<input type="number" v-model="phone" placeholder="请输入您的手机号" class="information_phone" maxlength="11" @focus="onInput" @blur="noInput">
				</view>
				<view class="code" v-if="yzm == true">
					<view class="code_text">验证码</view>
					<input v-model="code" placeholder="请输入验证码" class="information_code"  @focus="onHeight" @blur="noInput"  maxlength="4">
					<view class="send_code" @click="send_Code" type=“default” :disabled="!show" :style="!timer ? '':'background:#cccccc'" >{{!timer ? '发送验证码':count+'s'}}</view>
				</view>
			</view>
		</view>
		<view class="btn">
			<view class="back" @click="back">返回</view>
			<view class="jump" @click="jump">{{company == 0 ? '提交':'确定'}}</view>
		</view>
		<view class="caption" :style="fu == true ? '':{'top':lxfsTop}">
			<view class="corporate_name">协成科技有限公司 版权所有</view>
			<view class="qq">QQ:985424414</view>
		</view>
		<bottomBar :style="fu == true ? '':{top:bottomBarTop}"></bottomBar>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				code:'',
				img:"http://qp9mr1fhs.hn-bkt.clouddn.com/img/login.jpg",
				name:"狂爵心情",
				phone:"15362742943",
				oldphone:"15362742943",
				yzm:false,
				fu:true,
				lxfsTop:'',
				bottomBarTop:'',
				model:'',
				company:'',
				TIME_COUNT:60,//验证码倒计时60s
				count:'',
				show:true,
			}
		},
		onLoad(res) {
			let _this = this
			// console.log("company",res.company)
			
			// console.log("yemiancompany",_this.company)
			if(res.company == undefined){
				
			}else{
				// console.log("fsfsfaf")
				_this.company = res.company
				uni.setNavigationBarTitle({
				　　title:'完善个人资料'
				})
			}
			
			// this.company = res.company
		},
		methods: {
			
			back(){
				uni.navigateBack({
					
				})
			},
			jump(){
				// console.log("APP",this.company)
				if(this.name == "" || this.phone == ""){
					uni.showModal({
						title:"提示",
						content:"您的个人信息未填写完整\n请填写完整后再点击\n",
						showCancel:false,
						confirmColor: '#0ABB9A'
					})
				}else if(this.name == "" || this.phone == "" || this.company == 0){
					uni.showModal({
						title:"提示",
						content:"修改成功",
						showCancel:false,
						confirmColor: '#0ABB9A'
					})
				}else{
					uni.showModal({
						title:"提示",
						content:"您已成功加入该公司",
						showCancel:false,
						confirmColor: '#0ABB9A'
					})
				}
			},
			handleChooseImg(e){
				console.log(e)
				this.img = e.src
			},
			onInput(e){
				console.log(e)
				// if(this.phone != '' && this.phone != this.oldphone){
					this.yzm = true
					this.fu = false
				// }
				
			},
			onHeight(e){
				this.fu = false
			},
			noInput(e){
				this.fu = true
				if(this.phone == this.oldphone || this.phone == ''){
					this.yzm = false
					
				}
			},
			// Input(e){
			// 	if(this.phone == '' || this.phone == this.oldphone){
			// 		this.yzm = false
			// 	}else{
			// 		this.yzm = true
			// 	}
			// }
			send_Code(){
				if(this.phone.length < 11){
					uni.showToast({
						icon:'none',
						title:'手机号输入有误'
					})
				}else{
					if (!this.timer) {
						this.count = this.TIME_COUNT;
						this.show = false;
						uni.showModal({
							title:"提示",
							content:"手机二维码已发送\n请你打开短信查看\n",
							showCancel:false,
							confirmColor: '#0ABB9A'
						})
						this.timer = setInterval(() => {
						  if (this.count > 0 && this.count <= this.TIME_COUNT) {
							this.count--;
						  } else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						  }
						}, 1000)
					  }
				}
			},
			onResize(e){
				alert(e)
			}
		},
		onReady() {
			let _this = this
			uni.getSystemInfo({
				success: function (res) {
					console.log(res)
					_this.lxfsTop = res.windowHeight -88 +'px'
					_this.bottomBarTop = res.windowHeight - 56 + 'px'
					// console.log(_this.Top)
					_this.model = res.model
				}
			})
			
		}
	}
</script>

<style lang="scss">
	page{
		background-color: transparent;
	}
	.modify{
		.bg{
			width: 100%;
			height: 100%;
			z-index: -1;
			position: fixed;
			top: 0;
			left: 0;
		}
		.information{
			width: 90%;
			background-color: #FFFFFF;
			border-radius: 30rpx;
			margin: 50rpx auto;
			box-shadow: 0 0 15rpx #d0d5dd;
			.text{
				padding-top: 20rpx;
				text-align: center;
				font-size: 38rpx;
			}
			.content{
				display: flex;
				flex-wrap: wrap;
				font-size: 30rpx;
				.img{
					width: 100%;
					display: flex;
					justify-content: space-between;
					margin: 20rpx 30rpx;
					padding-bottom: 20rpx;
					align-items: center;
					border-bottom: 1rpx solid #f1f1f1;
					.information_img{
						border-radius: 50%;
					}
				}
				.name{
					width: 100%;
					display: flex;
					justify-content: space-between;
					margin: 30rpx;
					padding-bottom: 30rpx;
					align-items: center;
					border-bottom: 1rpx solid #f1f1f1;
					.information_name{
						text-align: right;
						font-weight: 700;
					}
				}
				.phone{
					width: 100%;
					display: flex;
					justify-content: space-between;
					margin: 30rpx;
					padding-bottom: 30rpx;
					align-items: center;
					.information_phone{
						text-align: right;
						font-weight: 700;
					}
				}
				.code{
					width: 100%;
					display: flex;
					justify-content: space-between;
					margin: 30rpx;
					padding-bottom: 30rpx;
					align-items: center;
					.code_text{
						white-space:nowrap !important;
					}
					.information_code{
						text-align: right;
						font-weight: 700;
					}
					.send_code{
						background-color: #0abb9a;
						white-space:nowrap;
						margin-left: 10rpx;
						padding: 20rpx;
						font-size: 24rpx;
						color: #FFFFFF;
						border-radius: 15rpx;
					}
				}
			}
		}
		.btn{
			display: flex;
			justify-content: space-around;
			font-size: 32rpx;
			margin: 0 60rpx;
			.back{
				border-radius: 20rpx;
				padding: 10rpx 55rpx;
				color: #0abb9a;
				background-color: #FFFFFF;
				border: 2rpx solid #0abb9a;
			}
			.jump{
				border-radius: 20rpx;
				padding: 10rpx 55rpx;
				background-color: #0abb9a;
				color: #FFFFFF;
			}
		}
		.caption{
			position: fixed;
			line-height: 40rpx;
			bottom: 80rpx;
			left: 50%;
			transform: translate(-50%,-50%);
			text-align: center;
			color: #C0C4CC;
			font-size: 22rpx;
			z-index: -1;
		}
	}
	
</style>
